<template>
  <div class="page-owasp-wrap">
    <el-row type="flex" class="row-bg" justify="space-between">
      <el-col :span="24">
        <el-row type="flex" class="row-bg" justify="space-between">
          <el-col :span="12">
            <h3>拟态防御配置</h3>
          </el-col>

        </el-row>
      </el-col>
    </el-row>
    <el-divider />
    <div class="margin-4x"></div>
    <el-row v-loading.fullscreen.lock="loadingPage">
      <el-col :span="24">
        <el-form :model="MimeticForm" :rules="rules" ref="MimeticForm" label-width="160px">
          <el-form-item label="拟态防御" key="1">
            <el-switch v-model="MimeticForm.mimetic_defense" active-value="true" inactive-value="false">
            </el-switch>
            
          </el-form-item>
          
          <div v-if="MimeticForm.mimetic_defense == 'true'">
            <el-form-item label="蜜罐IP" prop="proxy_host" key="2">
              <el-input v-model="MimeticForm.proxy_host" placeholder="请输入蜜罐IP">
              </el-input>
            </el-form-item>
            <el-form-item label="蜜罐端口" prop="proxy_port" key="3">
              <el-input v-model="MimeticForm.proxy_port" placeholder="请输入蜜罐端口">
              </el-input>
            </el-form-item>
            <el-form-item label="蜜罐Token" key="4" prop="token">
              <el-input v-model="MimeticForm.token" placeholder="请输入蜜罐Token">
              </el-input>
            </el-form-item>
          </div>

          
        </el-form>
        <div class="margin-4x"></div>
        <el-row type="flex" class="row-bg" justify="space-between">
          <el-col :span="24">
            <el-row type="flex" class="row-bg" justify="space-between">
              <el-col :span="12">

              </el-col>
              <el-col :span="12" class="text-align-right">
                <el-button type="primary" @click="onClickMimeticSubmit('MimeticForm')" :loading="loading">保存
                </el-button>
              </el-col>
            </el-row>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import { mixin, JXAjax, validatePort, validateIP } from "../assets/scripts/common";
export default {
  mixins: [mixin],
  data() {
    return {
      loadingPage: false,
      loading: false,
      MimeticForm: {},
    };
  },
  computed: {
    rules() {
      return {
        proxy_host: [
          {
            required: true,
            message: '请输入蜜罐IP',
            trigger: ["blur", "change"],
          },
          {
            validator: validateIP,
            trigger: ["blur", "change"],
          },
        ],
        proxy_port: [
          {
            required: true,
            message: '请输入蜜罐端口',
            trigger: ["blur", "change"],
          },
          {
            validator: validatePort,
            trigger: ["blur", "change"],
          },
        ],
        token: [
          {
            required: true,
            message: '请输入蜜罐Token',
            trigger: ["blur", "change"],
          },
        ],
      }
    }
  },

  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      var t = this;
      var getUrl = "/waf/waf_get_sys_mimetic_defense_conf";

      JXAjax(
        "post",
        getUrl,
        {},
        function (response) {
          t.loadingPage = false;
          t.MimeticForm = response.data.message;
        },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
    },

    onClickMimeticSubmit(MimeticForm) {
      var t = this;
      var postUrl = "/waf/waf_edit_sys_mimetic_defense_conf";
      this.$refs[MimeticForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.MimeticForm,
            function (response) {
              t.loading = false;
              t.getData();
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
  },
};
</script>
<style>
.page-owasp-wrap {
  max-width: 800px;
  min-width: 400px;
}

.page-owasp-wrap .match-inline-block {
  width: 192px;
}

.engine-form .el-form-item__content {
  margin-left: 40px;
}
</style>
